* {
  box-sizing: border-box;
}
bodyÂ  {
  margin: 0;
}

h1,
p {
  font-family: Lato;
}

main {
  padding: 36px;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 65px;
  justify-content: space-between;
  background: linear-gradient(90deg, #672280, 1.18%, #a626d3 100%);
  padding: 20px;
  color: white;
}

.header--image {
  height: 100%;
  margin-right: 6px;
}

.header--title {
  font-size: 1.25rem;
  margin-right: auto;
}

.header--project {
  font-size: 0.75rem;
  font-weight: bold;
}

.form {
  display: grid;
  grid-template: 40px 40px / 1fr 1fr;
  gap: 17px;
}

.form--input {
  border-radius: 5px;
  border: 1px solid #d5d4d8;
  text-indent: 5px;
}

.form--button {
  grid-column: 1 / -1;
  border-radius: 5px;
  background: linear-gradient(90deg, #672280, 1.18%, #a626d3 100%);
  color: white;
  border: none;
  cursor: pointer;
}

.meme {
  position: relative;
}

.meme--image {
  max-width: 100%;
  height: auto;
  margin: 16px auto;
  border-radius: 10px;
}

.meme--text {
  position: absolute;
  color: white;
  text-shadow: teal;
  font-weight: bold;
  text-align: center;
  width: 100%;
  padding: 16px;
  text-transform: uppercase;
  background: transparent;
  font-size: 3em;
  -webkit-text-stroke: 2px #6e0e0e;
}

.meme--text.top {
  top: 0;
}

.meme--text.bottom {
  bottom: 0;
}